textarea {
    width: 100%;
    background: #FAFAFA;
    border: 1px solid #8797C8;
    box-sizing: border-box;
    border-radius: 10px;
    padding: .5em;
    min-height: 100px;
    color: #757575;
}

textarea:active,
textarea:focus,
textarea:focus-visible,
textarea:focus-within,
textarea:hover {
    outline: none;
    color: #57699C;
}